.container { 
    display: flex;
    flex-direction: column;
    width: '100%';
    max-height: '100vh'; 
    align-items: center;
}

.controls {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 55%;
}

.datasetRadiobuttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
}
.datasetBoxContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.datasetRadiobuttons > * {
    width: 30%;
}

.characterSelectionMenu, .characterSelectionMenu div {
    display: flex;
    flex-direction: row;
} 

.characterSelectionMenu {
    width: 50%;
    flex-wrap: wrap;
    justify-content: space-around;
}

.characterSelectionMenu div {
    flex: 1 0 21%;
    justify-content: center;
}
